import {TextareaAutosize} from '@material-ui/core'
import Button from '@material-ui/core/Button'
import {makeStyles} from '@material-ui/core'
import InputBase from '../../../components/input'
import InputSelectApi from '../../../components/input_select_api'

const useStyles = makeStyles({
  container_create_estimate_calculation: {
    width: '50%',
    padding: '32px 16px',
    margin: '1rem auto',
    background: 'white',
    borderRadius: '4px',
    '&>div:nth-child(3)': {
      '&>label': {
        display: 'inline-block',
        marginBottom: '10px',
        marginTop: '10px',
        fontWeight: '500',
        fontSize: '18px',
        lineHeight: '27px',
        color: '#1F293',
      },
    },
    '&>div:nth-child(7)': {
      '&>p': {
        fontSize: '18px',
      },
    },
    '&>div:nth-child(8)': {
      textAlign: 'center',
      '&>button': {
        width: '120px',
        height: '48px',
        background: '#0065F2',
        borderRadius: '6px',
        fontWeight: 700,
        fontSize: '16px',
        lineHeight: '19px',
        textAlign: 'center',
        color: '#FFFFFF',
        margin: '1rem 1rem 2rem',
      },
    },
  },
})

const CreateEstimateCalculation = () => {
  const classes = useStyles()

  return (
    <div className={classes.container_create_estimate_calculation}>
      <InputSelectApi
        onChange={() => console.log(1)}
        label=''
        placeholder='  '
      />
      <InputBase
        onChange={() => console.log(1)}
        label=''
        placeholder=' .'
      />
      <div style={{display: 'inherit'}}>
        <label htmlFor='textarea'>  </label>
        <br />
        <TextareaAutosize
          aria-label='minimum height'
          minRows={3}
          id='textarea'
          placeholder=':     '
          style={{
            width: '100%',
            paddingLeft: '10px',
            height: '144px',
            boxSizing: 'border-box',
          }}
        />
      </div>
      <InputBase
        onChange={() => console.log(1)}
        label=''
        placeholder='  '
      />
      <InputSelectApi
        onChange={() => console.log(1)}
        label=''
        placeholder='   '
      />
      <InputBase
        onChange={() => console.log(1)}
        label=' URL'
        placeholder=' '
        icon={<></>}
      />
      <div>
        <p> </p>
        {/* <UploadImages /> */}
      </div>
      <div>
        <Button></Button>
      </div>
    </div>
  )
}

export default CreateEstimateCalculation
